<template>
  <div id="app">
    <h1> [[원예/광부 한정] 이슈가르드 부흥 납품 채집 점수아트 계산기] </h1>
    <a href="https://atez.tistory.com/63">어떻게 만들었나요..?</a>
    <h3> 제작 : 톤베리@테즈 / 도움 : pula39(github.com/pula39) / 아이디어 : 톤베리@에픠</h3>
    <div>
      <p> 숫자에 반점 찍지 마시구, 그대로 132482처럼 입력해주세요.</p>
      <p> 너무 오래걸린다면? 목표 점수와 현재 점수의 차이가 너무 큰 경우.. 안댈수도 있어용</p>
      <p>현재 점수를 입력해주세요</p>
      <input v-model="now_score" placeholder="현재 점수를 입력해주세요" @keypress="isNumber($event)">
    </div>
    <div>
      <p>목표 점수를 입력해주세요</p>
      <input v-model="end_score" placeholder="목표 점수를 입력해주세요" @keypress="isNumber($event)">
    </div>
    <div>
      <p>10점짜리 묶음 최대 개수를 입력해주세요.</p>
      <input v-model="x_mx" placeholder="10점짜리 묶음 최대 개수를 입력해주세요." @keypress="isNumber($event)">
    </div>
    <div>
      <p>35점짜리 묶음 최대 개수를 입력해주세요.</p>
      <input v-model="z_mx" placeholder="35점짜리 묶음 최대 개수를 입력해주세요." @keypress="isNumber($event)">
    </div>
    <br><br>
    <button v-on:click="calcTuples">계산하기</button>
    <h3> 예시) "10 3280 8" 이 나왔다면
    <h3> 차례대로 10점 짜리 100개, 32점 짜리 32800개, 35점짜리 80개를 납품하시면 됩니다.</h3>
    <h4> {{ msg }} </h4>
    <div v-for="result in results" v-bind:key="result.x">
      <h3>
      {{result.x}}
      {{result.y}}
      {{result.z}}
      </h3>
    </div>
  </div>
</template>

<script>
// export default = 내가 주는 모듈의 기본이 이거다.
export default {
  name: 'App',
  // component에서 넣어줘야 template
  components: {},
  data () {
    return {
      now_score: 0,
      end_score: 0,
      x_mx: 0,
      z_mx: 0,
      results:[],
      msg: ""
    }
  },
  methods: {
    isNumber: function(evt) {
      evt = (evt) ? evt : window.event;
      var charCode = (evt.which) ? evt.which : evt.keyCode;
      if ((charCode > 31 && (charCode < 48 || charCode > 57))) {
        evt.preventDefault();;
      } else {
        return true;
      }
    },
    calcTuples: function() {
      this.msg = "계산중입니다..."
      var m = parseInt(this.end_score - this.now_score)
      var x_fr = parseInt(m*0.4);
      var x_to = parseInt((52.0/128.0)*m);
      var y_fr = parseInt((-0.8/7.0)*m);
      var y_to = parseInt(0);
      var x,y,z;
      var lst = []
      var k,l;
      for (k = parseInt(x_fr-1); k <= parseInt(x_to)+1; k++) {
        for (l = parseInt(y_fr-1); l <= parseInt(y_to)+1; l++) {
          x = 52 * m - 128 * k + 7 * l
          y = 5 * k - 2 * m
          z = 32 * k - 13 * m - 2 * l
          if (!(x >= 0 && y >= 0 && z >= 0)) continue
          if ((x<=this.x_mx)&&(z <=this.z_mx)) {
            lst.push({x:x, y:y, z:z})
          }
        }
      }

      this.results = lst;
      this.msg = "계산 완료."
    }
  },
  computed: {
    
  }
}
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
